body {
    margin: 0;
    font-family: 'Arial', sans-serif;
  }
  
  .top-bar {
    background-color: #000;
    color: #fff;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  

  .box {
    width: 150px;
    height: 150px; 
    border-radius: 70%;
    overflow: hidden;
}
   .profile {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

  .logo {
    font-size: 24px;
  }
  
  .search-box {
    display: flex;
    align-items: center;
  }
  
  .search-box input {
    padding: 5px;
    margin-right: 5px;
  }
  
  .search-button {
    background: none;
    border: none;
  }
  
  .icons button {
    background: none;
    border: none;
    margin-left: 10px;
  }
  
  .body-container {
    background-color: #fff;
    padding: 20px;
    display: flex;
  }
  
  .profile-section {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .profile-section img {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    margin-bottom: 10px;
  }
  
  .user-info {
    text-align: center;
  }
  
  .posts-section {
    margin-left: 20px;
    width: 100%;
  }
  
  .post {
    margin-bottom: 20px;
  }
  
  .post-title {
    font-size: 18px;
    font-weight: bold;
    margin-bottom: 10px;
  }
  
  .post-content {
    margin-bottom: 10px;
  }
  
  .comments {
    margin-top: 10px;
  }
  
  .comment {
    margin-bottom: 5px;
  }